@import "../../styles/config";

.header-wrap {
  width: 100%;
  height: $header-height;
  background: $header-color;
  display: flex;
  justify-content: space-between;
  .icon-wrap {
    width: 40px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    transition: .5s ease;
    margin: 0 10px;
    border-radius: 2px;
    &:hover {
      background: white;
      color: black; } }
  .left-side, .right-size {
    display: flex;
    justify-content: flex-start;
    flex-direction: row;
    align-items: center; }
  .right-size {
    justify-content: flex-end; } }
